<template>
  <div class="container">
    <!-- 固定导航 -->
    <van-nav-bar fixed left-arrow @click-left="$router.back()" title="聊天机器人"></van-nav-bar>

    <!-- 聊天主体区域 -->
    <div class="chat-list">
      <!-- 在第二个div上进行循环，根据判断结果来决定显示左边还是右边 -->
      <div v-for="(item, index) in chatList" :key="index">
        <!-- 左侧是机器人 -->
        <div class="chat-item left" v-if="item.name !== 'user'">
          <!-- <van-image fit="cover" round src="" /> -->
          <img src="../../assets/kefu.jpg" alt="" class="van-image" />
          <div class="chat-pao">{{ item.message }}</div>
        </div>

        <!-- 右侧是当前用户 -->
        <div class="chat-item right" v-else>
          <div class="chat-pao">{{ item.message }}</div>
          <van-image fit="cover" round :src="$store.state.userPhoto" />
        </div>
      </div>
    </div>

    <!-- 对话区域 -->
    <div class="reply-container van-hairline--top">
      <van-field placeholder="说点什么..." v-model="userInputMessage">
        <template #button>
          <span style="font-size: 12px; color: #999" @click="sendMessageFn">提交</span>
        </template>
      </van-field>
    </div>
  </div>
</template>

<script>
// 1 引入io这个方法，从socket.io-client包里
import { io } from 'socket.io-client'
export default {
  data() {
    return {
      // 用户输入的信息
      userInputMessage: '',
      // 聊天信息列表
      chatList: [
        {
          name: 'robot',
          message: '你好呀'
        }
      ],
      // 2 创建一个socket对象
      socket: null
    }
  },
  // 3 在页面一创建的时候就和websocket建立连接
  created() {
    this.socket = io('http://geek.itheima.net', {
      query: {
        token: localStorage.getItem('token')
      },
      transports: ['websocket']
    })
    // 4 测试是否建立成功
    this.socket.on('connect', () => {
      console.log('建立连接成功')
    })
    // 5 接收后端传回的消息
    this.socket.on('message', (obj) => {
      this.chatList.push({
        name: 'robot',
        // 这里返回的数据一开始没有，因为你写的obj.message，返回的数据一定要用服务器返回的格式
        message: obj.msg
      })
    })

    // 需求：希望消息数组改变的时候就立刻滚动到最后一条消息的位置
    this.$nextTick(() => {
      const lastChatDiv = document.querySelector('.chat-list>div:last-child')
      // console.log(lastChatDiv)
      // 问题：数据变化是实时更新的，但是Dom更新是异步的，所以只能取到上一条div,所以要么加定时器，要么用vue的nextTick()方法
      lastChatDiv.scrollIntoView({
        behavior: 'smooth'
      })
    })
  },
  methods: {
    // 6 从前端发送消息到后端
    sendMessageFn() {
      // 首先预校验输入内容
      if (this.userInputMessage.trim().length === 0) return
      this.socket.emit('message', {
        msg: this.userInputMessage,
        timestamp: new Date().getTime()
      })
      // 把消息显示到页面上
      this.chatList.push({
        name: 'user',
        message: this.userInputMessage
      })
      // 需求：希望消息数组改变的时候就立刻滚动到最后一条消息的位置
      this.$nextTick(() => {
        const lastChatDiv = document.querySelector('.chat-list>div:last-child')
        // console.log(lastChatDiv)
        // 问题：数据变化是实时更新的，但是Dom更新是异步的，所以只能取到上一条div,所以要么加定时器，要么用vue的nextTick()方法
        lastChatDiv.scrollIntoView({
          behavior: 'smooth'
        })
      })
      // 清空输入框
      this.userInputMessage = ''
    }
  },
  // 在组件销毁的时候要断开和socket的通讯并将socket对象重置为空，不然会浪费服务器的资源
  destroyed() {
    this.socket.close()
    this.socket = null
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background: #fafafa;
  padding: 46px 0 50px 0;
  .chat-list {
    height: 100%;
    overflow-y: scroll;
    .chat-item {
      padding: 10px;
      .van-image {
        vertical-align: top;
        width: 40px;
        height: 40px;
      }
      .chat-pao {
        vertical-align: top;
        display: inline-block;
        min-width: 40px;
        max-width: 70%;
        min-height: 40px;
        line-height: 38px;
        border: 0.5px solid #7bc6ff;
        border-radius: 4px;
        position: relative;
        padding: 0 10px;
        background-color: #7bc6ff;
        word-break: break-all;
        font-size: 14px;
        color: #333;
        &::before {
          content: '';
          width: 10px;
          height: 10px;
          position: absolute;
          top: 12px;
          border-top: 0.5px solid #7bc6ff;
          border-right: 0.5px solid #7bc6ff;
          background: #7bc6ff;
        }
      }
    }
  }
}
.chat-item.right {
  text-align: right;
  .chat-pao {
    margin-left: 0;
    margin-right: 15px;
    // border: 0.5px solid #e0e0e0;
    // background-color: #e0e0e0;
    // border-top: 0.5px solid #e0e0e0;
    // border-right: 0.5px solid #e0e0e0;
    // background: #e0e0e0;
    &::before {
      right: -6px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left {
  text-align: left;
  .chat-pao {
    margin-left: 15px;
    margin-right: 0;
    // border: 0.5px solid #7bc6ff;
    // background-color: #7bc6ff;
    // border-top: 0.5px solid #7bc6ff;
    // border-right: 0.5px solid #7bc6ff;
    // background: #7bc6ff;

    &::before {
      left: -5px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 44px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
}
</style>
